import React, { Component } from "react";
//import { connect } from "redux-zero/react";
import "./Header.css";
import { NavLink } from 'react-router-dom';
import Aboutme from './Aboutme';
import Skills from './Skills';
import Works from './Works';
import Contact from './Contactme';
import foto from './IMG_4978.jpg';

class Header extends Component {
    render(){
        return(
            <div className="headernav">
            <div className="navbar-wrapper">
            <div className="k">
      
              <nav className="navbar navbar-inverse navbar-static-top btns">
                <div className="dss">
                  <div className="navbar-header">
                    <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#navbar" aria-expanded="false" aria-controls="navbar">
                      <span className="sr-only">Toggle navigation</span>
                      <span className="icon-bar"></span>
                      <span className="icon-bar"></span>
                      <span className="icon-bar"></span>
                    </button>
                    
                  </div>
                  <div id="navbar" className="navbar-collapse collapse">
                    <ul className="nav navbar-nav">
                      <li className="active"><NavLink exact to="/home"><i class="fa fa-home hidden-lg fa-3x" aria-hidden="true"></i><button className="hidden-xs col-lg-12 col-md-12 col-sm-12 col-xs-12 navtop first-btn custom-btn btn-6">Home</button></NavLink></li>
                      <li className="active"><NavLink exact to="/aboutme"><i class="fa fa-user hidden-lg fa-3x" aria-hidden="true"></i><button className="hidden-xs col-lg-12 col-md-12 col-sm-12 col-xs-12 navtop first-btn custom-btn btn-6">About me</button></NavLink></li>
                      <li className="active"><NavLink exact to="/skills"><i class="fa fa-cog hidden-lg fa-3x" aria-hidden="true"></i><button className="hidden-xs col-lg-12 col-md-12 col-sm-12 col-xs-12 navtop second-btn custom-btn btn-6">Skills</button></NavLink></li>
                      <li className="active"><NavLink exact to="/works"><i class="fa fa-file-code-o hidden-lg fa-3x" aria-hidden="true"></i><button className="hidden-xs col-lg-12 col-md-12 col-sm-12 col-xs-12 navtop third-btn custom-btn btn-6">Works</button></NavLink> </li>
                      <li className="active"><NavLink exact to="/contact"><i class="fa fa-phone hidden-lg fa-3x" aria-hidden="true"></i><button className="hidden-xs col-lg-12 col-md-12 col-sm-12 col-xs-12 navtop fourth-btn custom-btn btn-6">Contact</button></NavLink></li>
                    </ul>
                  </div>
                </div>
              </nav>
      
            </div>
          </div>
        </div>
        )
    }
}

export default Header;


