import React, { Component } from 'react';
import Header from './Header';
import Header2 from './Header2';
import foto2 from './IMG_4978.jpg';
import './Aboutme.css';
// import Header from './Header';

class Aboutme extends Component {
  render() {
    return (
    <div className="App">
          <Header/>
      <div className="row">
        <div className="col-lg-4 col-xs-12">
          <img className="imgaboutme hidden-xs" src={foto2} />
        </div>
          {/* <img className="image2 img-responsive" src={foto2}/> */}
        <div className="col-lg-8 col-xs-12">
        <section className="row">
          <div className="title"><span><div className="titleaboutme ">About Me</div></span><span><h4>Front-end</h4><a className="a1 hidden-xs">Developer</a><a className="iphone5 hidden-lg">Developer</a>
              <h4>I'm mother, student of laboratoria and industrial engineering.
              I love learning and contributing with all the things that I know,
              I have many ideas that I previously were thought impossible and since
               I know the world of technology I have opened up to more possibilities
 and I have learned to develop technical skills as well as my soft skills.</h4>
            </span></div>
            
        </section>
        </div>
        </div>
        <Header2/>
    </div>
    );
  }
}

export default Aboutme;



