import React, { Component } from 'react';
import './Skills.css';
import Header from './Header';
import Header2 from './Header2';
import {NavLink} from 'react-router-dom';
import foto2 from './IMG_4978.jpg';
import proactiva from './proactiva.png';
import psquad from './psquad.png';

class Skills extends Component {
  render() {
    return (
    <div className="App">
      <Header/>
      <div className="row">
      <div className="col-lg-3 col-sm-3 skillsdiv"> 
        <section className="section hidden-xs">
          <div className="title">
            <span>Skills</span>
            <span>Technical<a className="a2">-Soft</a></span>
          </div>
        </section>
      </div>
      <div className="col-lg-9 col-sm-9 skillsdiv1">
          <div className="row title">   
            <span>
          <div class="wrap">
            <div class="section-title wow hidden-lg fadeInUp skillstitle">
              Skills
              <span class="border"></span>
            </div>
            <div class="row ull">
                <div className="col-lg-4 col-sm-12 html div1">
                  <em>HTML/HTML5</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div> 
                <div className="col-lg-4 col-sm-12 css div1">
                <em>CSS/CSS3</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div> 
                <div className="col-lg-4 col-xs-12 php div1">
                <em>JAVASCRIPT</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div>     
              </div>
              <div class="row ull">
                <div className="col-lg-4 col-xs-12 mysql div1">
                <em>JQUERY</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div>
                <div className="col-lg-4 col-xs-12 jquery div1">
                <em>BOOTSTRAP</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div>
                <div className="col-lg-4 col-xs-12 javascript div1">
                <em>REACT</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div>
              </div>
          <div class="row ull">
              <div className="col-lg-4 col-xs-12 seo div1">
                <em>FIREBASE</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div>
                <div className="col-lg-4 col-xs-12 unix div1">
                <em>SASS</em>
                  <div>
                    <span class="animated fadeInLeft"></span>
                  </div>
                </div>
            </div>
            <div className="row">
              <div className="col-lg-6 text-right">
            <img className="skills" src={proactiva}/>
            </div>
            <div className="col-lg-6">
            <img className="skills" src={psquad}/>
            </div>
            </div>
            </div>
            </span>
          </div>
      </div>
      </div>
    </div>
    );
  }
}

export default Skills;


